import {
    Box,
    Button,
    ButtonGroup,
    Container,
    Flex,
    HStack,
    IconButton,
    useBreakpointValue,
    useColorModeValue,
    Image,
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    MenuItemOption,
    MenuGroup,
    MenuOptionGroup,
    MenuDivider,
    Badge,
    Stack,
    VStack,
    Text,
    Spacer
  } from '@chakra-ui/react';
  import * as React from 'react';
  
  
  export const Nav = () => {
    const isDesktop = useBreakpointValue({
      base: false,
      lg: true,
    });
  
  
    return (
      <Box
        as="nav"
        bg="bg-surface"
        // boxShadow={useColorModeValue('sm', 'sm-dark')}
        marginBottom="2"
        p="2"
      >
        <HStack spacing="10" justify="space-between">
          <HStack>
           <h2>logo</h2>
          </HStack>
          {isDesktop ? (
            <Flex w="100%" align="center" justify="flex-end">
              <HStack as="nav" spacing="5">
               
                  <h3>Home</h3>
               
            
                  <h3>About</h3>
              
               <h2>Projects</h2>
              </HStack>
              {/* // Call to action items */}
            </Flex>
          ) : (
            <>
            <Spacer />
             hamburgerMenu
            </>
          )}
        </HStack>
      </Box>
    );
  };
  